const qs = document.querySelector.bind(document);

const shippingEstimator = function() {
  const shippingEstimator = qs('.shipping_estimator_wrapper')
  if (!shippingEstimator) return;
  const submitButton = qs('.panel__form');
  const country = qs('.shipping_estimator_panel [name="country"]');
  const province = qs('.shipping_estimator_panel [name="province"]');
  const loadProvinces = () => {
    const selectedCountryProvinces = JSON.parse(
      country.options[country.selectedIndex].getAttribute('data-provinces'),
    );
    province.innerHTML = ''; // clear all province options when country is changed

    if (selectedCountryProvinces.length) {
      province.style.display = "initial";
      selectedCountryProvinces.forEach((p) => {
        province.options.add(new Option(p[1], p[0]));
      });
    } else {
      province.style.display = "none";
    }
  };
  const fetchRates = () => {
    const country = qs('.shipping_estimator_panel [name="country"]').value;
    const province = qs('.shipping_estimator_panel [name="province"]').value;
    const zipCode = qs('.shipping_estimator_panel [name="zip_code"]').value;
    const fetchUrl = `/cart/shipping_rates.json?shipping_address[zip]=${zipCode}&shipping_address[country]=${country}&shipping_address[province]=${province}`;
    const resultsContainer = qs('.shipping_estimator__results');
    const errorContainer = qs('.shipping_estimator__error');

    fetch(fetchUrl, { credentials: 'same-origin', method: 'GET' })
      .then((response) => response.json()
        .then((result) => {
        // Clear previous results
          resultsContainer.firstElementChild.innerHTML = '';
          errorContainer.innerHTML = '';
          resultsContainer.style.display = 'initial';
          errorContainer.style.display = 'none';
          resultsContainer.children[0].classList.remove('result-visible');
          if (response.ok) {
            const shippingRates = result.shipping_rates;
            if (!shippingRates.length) {
              resultsContainer.firstElementChild.innerHTML = '<p>No shipping could be found</p>';
            } else {
              const shippingRateTemplates = shippingRates.map((rate) => `<li>${rate.name}: $${rate.price}</li>`).join(' ');

              const resultTemplate = `
              <p>${shippingRates.length} option${shippingRates > 1 ? 's' : ''} available:</p>
              <ul>
                ${shippingRateTemplates}
              </ul>
            `;

              resultsContainer.firstElementChild.innerHTML = resultTemplate;
              resultsContainer.children[0].classList.add('result-visible');
            }
          } else {
            let errorHtml = '';
            Object.keys(result).forEach((key) => {
              errorHtml += `<li class="Alert__ErrorItem"> ${key} ${result[key]} </li>`;
            });

            errorContainer.innerHTML = `<ul class="Alert__ErrorList">${errorHtml}</ul>`;
            resultsContainer.style.display = 'none';
            errorContainer.style.display = 'block';
          }
        }));
  };
  loadProvinces();
  country.addEventListener('change', loadProvinces);
  submitButton.addEventListener('submit', (e) => {
    e.preventDefault();
    fetchRates();
  });
};

export default shippingEstimator;
