import Swiper, { Navigation } from 'swiper';
import { fetchRebuyRecommendedProducts } from '../services/products';
import 'swiper/swiper-bundle.css';
import {
  attachProductItemEvents,
  getProductItemBody,
  renderVideos,
} from '../common/product-item';
import { getCookie } from '../utils/dom';

const HIDE_CART_POPUP_DURATION = 3; // hours
const HIDE_COOKIE_NAME = '__hide_cart_recommend';

Swiper.use([Navigation]);

const state = {
  products: [],
  swiper: null,
  lineItems: [],
  elements: {},
  renderedProducts: {},
};

const init = async () => (
  (!getCookie(HIDE_COOKIE_NAME) && storageCookieExpired()) && (
    cacheElements(),
    attachListeners(),
    getLineItems(),
    state.lineItems.length && (
      await getRecommendedProducts(),
      initializePopupSwiper(),
      await renderProducts(),
      showPopup()
    )
  )
);

const storageCookieExpired = () => {
  const lastHideDate = localStorage.getItem(HIDE_COOKIE_NAME);
  if (!lastHideDate) return true;
  const dateParsed = new Date(lastHideDate).getTime();
  const currentDate = new Date().getTime();
  return dateParsed < currentDate;
};

const cacheElements = () => {
  const container = document.querySelector('cart-items.cart-page-items');
  state.elements = {
    container,
    popup: container.querySelector('.product-popup'),
    popupBody: container.querySelector('.product-popup__body'),
    closeBtn: container.querySelector('.product-popup__close-btn'),
    backdrop: container.querySelector('.product-popup__backdrop'),
  };
};

const attachListeners = () => {
  [state.elements.closeBtn, state.elements.backdrop]
    .forEach((e) => e.addEventListener('click', closePopup));
};

const getLineItems = () => {
  if (!state.elements.container) return;
  const rawJson = state.elements.container.dataset.sectionSettings;
  state.lineItems = JSON.parse(rawJson).lineItems;
};

const initializePopupSwiper = () => {
  state.swiper = new Swiper('.product-popup__body .swiper', {
    slidesPerView: 1,
    spaceBetween: 10,
    simulateTouch: false,
    navigation: {
      prevEl: '.product-popup .recommendations__btn--prev',
      nextEl: '.product-popup .recommendations__btn--next',
      disabledClass: 'recommendations__btn--disabled',
    },
    breakpoints: {
      1024: { slidesPerView: 2 },
      1600: { slidesPerView: 3 },
    },
  });
};

const renderProducts = async () => {
  const productItems = state.products.map((product, index) => `
    <div class="swiper-slide">
      ${getProductItemBody(product, index, state.renderedProducts)}
    </div>
  `);
  state.swiper.appendSlide(productItems);
  attachProductItemEvents(state.renderedProducts, state.elements.popupBody, true);
  await renderVideos(state.products, state.renderedProducts);
};

const showPopup = () => {
  state.elements.popup.classList.add('product-popup--active');
  document.documentElement.style.overflow = 'hidden';
};

const closePopup = () => {
  state.elements.popup.classList.remove('product-popup--active');
  document.documentElement.style.removeProperty('overflow');
  setCartCookies();
};

const getHideExpiration = () => {
  const date = new Date();
  date.setHours(date.getHours() + HIDE_CART_POPUP_DURATION);
  return date.toUTCString();
};

const setCartCookies = () => {
  if (
    getCookie(HIDE_COOKIE_NAME)
    || !storageCookieExpired()
  ) {
    return;
  }
  const expirationDate = getHideExpiration();
  if (/^((?!chrome|android).)*safari/i.test(navigator.userAgent)) {
    localStorage.setItem(HIDE_COOKIE_NAME, expirationDate);
    return;
  }
  document.cookie = `${HIDE_COOKIE_NAME}=true; expires=${expirationDate}; path=*`;
};

const getRecommendedProducts = async () => {
  const products = await fetchRebuyRecommendedProducts(state.lineItems, 7);
  state.products = await fetchRebuyRecommendedProducts(state.lineItems, 7);
};

export default init;
